@font-face{
    font-family: 'Qualio';
    src: url('../res/fonts/qualio.regular.ttf');
}
:root{
    --color-light: #fff8e7;
    --color-dark: #321A27;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body, html{
    width: 100%;
    height: 100vh;
    font-family: 'Qualio';
    color: var(--color-dark);
}
header{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 20%;
    padding: 1rem;
}
header section h1{
    margin: 0 0 1rem 0;
}
header section form {
    display: flex;
    flex-direction: row;
    width: 100%;
}
header section form > *{
    margin-right: .3rem;
}
header section form input{
    border-left: none;
    border-top: none;
    border-right: none;
    border-bottom: 1px solid var(--color-dark);
    padding: .5rem .8rem;
    outline: none;
    font-size: 1rem;
    flex: 1;
}
header section form button{
    font-size: 1.5rem;
    border: none;
    background: none;
}
header ul{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
header ul li{
    margin: 1rem 1rem 1rem 0;
    list-style: none;
    font-size: 1rem;
}
header ul li > *{
    margin-right: .5rem;
}

main{
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: row;
    width: 100%;
    height: 55%;
    padding: 2rem;
}
main .age-display{
    height: 100%;
    min-width: 1%;
    overflow-wrap: break-word;
    font-weight: 900;
    font-size: 2rem;
}
main .age{
    font-size: clamp(7.5rem, 10rem, 5vw);
}

.options{
    position: absolute;
    height: 100%;
    width: 100%;
    top: -100%;
    left:0;
    background-color: var(--color-dark);
    color: var(--color-light);
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-content: center;
    padding: 2rem;
    transition: all 500ms ease-in-out;
}
.options > *{
    margin: 1rem;
}
.options form{
    display: flex;
    flex-direction: column;
}
.options form section{
    margin: .5rem 0;
    display: flex;
    flex-direction: column;
}
.options form section > *{
    margin: .3rem;
}
.options form section input{
    padding: .5rem .8rem;
    border:none;
    border-bottom: 1px solid var(--color-light);
    background: none;
    color: var(--color-light);
    outline: none; 
    width: 100%;
}
.options form button{
    padding: .5rem .8rem;
    border-radius: 25px;
    border:none;
    outline: none;
    font-weight: 700;
}
.options .close{
    position: absolute;
    right: 5%;
    top: 2%;
    font-size: 2rem;
    font-weight: 900;
}